.heading-lg-tilted {
  @apply font-manrope font-bold text-px32 leading-px40 tracking-em005;

  & > tilt,
  & > tiltb {
	@apply inline-block font-bold text-px44 leading-px54 tracking-em005 -mt-4;
  }

  @screen md {
	@apply text-px36 leading-px42;

	& > tilt,
	& > tiltb {
	  @apply text-px46;
	}

	.type-horizontal & {
		@apply text-px32 leading-px40;

		& > tilt,
		& > tiltb {
			@apply text-px44 leading-px54 tracking-em005;
		}
	}
  }

  @screen lg {
	@apply text-px38 leading-px50 tracking-normal;

	& > tilt,
	& > tiltb {
	  @apply text-px48 leading-px70;
	}
  }

  @screen xl {
	@apply text-px52 leading-px62;

	& > tilt,
	& > tiltb {
	  @apply text-px70 leading-px80;
	}

	.type-horizontal & {
		@apply text-px56 leading-px66;

		& > tilt,
		& > tiltb {
			@apply text-px82 leading-px92;
		}
	}
  }


}

//
// Page Sub Title
//
.page-sub-title,
.page-sub-title-desc {
  @apply mb-3.5;

  &.centered {
	@apply mx-auto text-center;
  }

  @screen md {
	@apply mb-2 w-3/4;
  }

	@screen lg {
		&.lg\:no-centered {
			@apply text-left mx-0;
		}
	}

  @screen xl {
	@apply mb-3.5;

	&.xl\:no-centered {
	  @apply text-left mx-0;
	}
  }
}

.page-sub-title + .page-sub-title-desc {
  @screen md {
	@apply mt-2;
  }
}

.page-sub-title {
  @apply font-bold text-px32 leading-px40;

  @screen md {
	@apply leading-px42;
  }

  @screen xl {
	@apply text-px46 leading-px56 tracking-em_001;

	.is-single-page & {
	  @apply text-px42 leading-px52 tracking-em_001;
	}
  }
}

.page-sub-title-desc {
  @apply font-normal text-px18 leading-px24 tracking-px05;

  @screen xl {
	@apply text-px24 leading-px34;
  }
}
