.info-card {
  &-grid {
	@apply flex flex-col items-center mx-auto max-w-px319 my-14 md:max-w-screen-md xl:flex-row xl:my-16 xl:max-w-screen-xl;

	& > .info-card {
	  &:first-child {
		@apply xl:mr-3.5;
	  }

	  &:last-child {
		@apply xl:ml-3.5;
	  }

	  &:nth-child(2) {
		@apply mt-7 xl:mt-0;
	  }
	}
  }

  @apply mx-auto;
  @apply flex flex-col text-center;
  @apply py-20 px-7 md:py-24  md:px-px88 lg:py-20;
  @apply rounded-px10;
  max-width: rem-calc(500px);

  &-inner {
	@apply h-full w-full;
	@apply flex flex-col justify-center;
  }

  &-title {
	@apply mb-px20 font-bold text-px32 leading-px40 md:mb-3.5 md:leading-px42 mb-3;
	@apply xl:text-px36 xl:leading-px46;
  }

  &-title-desc {
	@apply font-normal text-px18 leading-px26 tracking-px05 md:text-px18 md:leading-px24 lg:text-px20 lg:leading-px26;

	@screen xl {
	  @apply mx-auto text-px20 leading-px30;
	  max-width: rem-calc(818px);
	}
  }

  &-btn-wrap {
	@apply mt-px50 md:mt-px50 lg:mt-px40;
	@apply flex flex-col md:flex-row flex-wrap items-center md:justify-center;
  }

  &-btn {
	& + & {
	  @apply mt-px20 md:mt-0 md:ml-px20 xl:ml-px18;
	}
  }
}
